@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


body,html{
    margin: 0;
    padding: 0;
    font-size: 16px;
    color: #333;
    font-family: 'Poppins', sans-serif!important;
 
}

*{
    box-sizing: border-box;
}


h1,h2,h3,h4,h5,h6{
    font-weight: 500;
    color: #333;
    text-transform: capitalize;
    line-height: 46px;
    margin: 15px 0;
}

:root{
    --prime-color : #4ac9f0;
    --second-color : #023041;
    --third-color :#c7b201;
}





/********************* Header *************************/

.header{
    width: 100%;
    height: auto;
    background-color: #023041;
    background-color: var(--second-color);
}

.cont span {
    margin-right: 6px;
}

.cont p {
    font-size: 14px;
}

.headIcon{
    width: 100%;
    height: auto;
}

.headIcon ul{
    list-style-type: none;
}

.headIcon ul li {
    width: 25px;
    height: 25px;
    border-radius: 50px;
    background-color: #000;
    display: flex;
    align-items: center;
    justify-content: center;
}

.headIcon ul li:hover{
  background-color: #c7b201;
  background-color: var(--third-color);
  color: #4ac9f0;
  color: var(--prime-color);   
  cursor: pointer;
}

.socI{
    font-size: 14px;
}


/***********************Nav****************************/

.navbar.bg-light{
    background:transparent!important;
}

.navbar-nav .nav-item {
    padding: 0 10px;
}

.navbar-nav .nav-link {
    color: #333;
    text-transform: capitalize;
    font-weight: 500;

}
.navbar-nav .btn-info{
    background-color: #4ac9f0;
    background-color: var(--prime-color);
    padding: 5px 17px;
    color: #fff;
}
.navbar .navbar-brand img{
    width: 12rem;
}


.navbar-nav .nav-link.active{
    color: #4ac9f0!important;
    color: var(--prime-color)!important;
}

.getQuote{
    padding: 5px 20px;
    background-color: #4ac9f0!important;
    background-color: var(--prime-color)!important;
    text-transform: uppercase;
    font-size: 13px!important;
    font-weight: 500!important;
    color: #fff!important;
   
}


/******************** hero *************************/
.hero{
    width: 100%;
    height: auto;
    background-image: url(/better_clean/static/media/hero.4193dc80.png);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top;
    position: relative;
    top: 0;
    left: 0;
}


/*********************** caption ****************************/
.caption{
    width: 100%;
    height: 66vh;
    padding: 0;
    margin: 0;
    display: flex;
    align-items: center;
}

.caption h1{
    font-size: 2rem;
    font-weight: 500;
    text-transform: uppercase;
    color: #333;
    margin: 0;
    line-height: 14px;
}

.caption h2{
    font-size: 2.5rem;
    font-weight: 700;
    text-transform: uppercase;
    color: #4ac9f0;
    color: var(--prime-color);
    margin: 0;
}

.caption p{
    width: 74%;
    height: auto;
    font-size: 15px;
    color: #333;
    margin: 20px 0;
}

.btnContact{
    background-color: #020607!important;
    padding: 5px 20px!important;
    color: #fff!important;
}

.btnContact:hover{
    background-color: #c7b201!important;
    background-color: var(--third-color)!important;
    color:  #020607!important;
}

/****************** maincard *********************/
.maincard{
    width: 100%;
    height: auto;
    padding: 0;
    background-color:#fff;
    display: flex;
    justify-content: space-between;
}
.cards{
    width: 32.7%;
    height: auto;
}
    

.cards .card{
    width: 100%;
    height: auto;
    padding: 57px 20px;
    background-color: #4ac9f0!important;
    background-color: var(--prime-color)!important;
    border: none!important;
    border-radius: 0!important;
    text-align: center;
}

.cards .card:hover{
    background-color: #c7b201!important;
    background-color: var(--third-color)!important;
    transition: all 0.5s;
    cursor: pointer;
}

.cards .card:hover span{
    background-color: #c7b201!important;
    background-color: var(--third-color)!important;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.card span{
    width: 60px;
    height: 60px;
    background-color: #4ac9f0;
    background-color: var(--prime-color);
    border-radius: 50px;
    color: #fff;
    font-size: 18px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    margin-top: -87px;
    font-weight: 500;
}

.card h4 {
    font-size: 20px;
    font-weight: 500;
    color: #fff;
    margin: 26px 0;
}
.card p{
    color: #fff;
    margin: 0;
}

/********************* wecome_section ***********************/

.wecome_section{
    width: 100%;
    height: auto;
}
.wecome_section h2{
    font-size: 2.3rem;
    text-transform: uppercase;
    color: #151515;
    font-weight: 700;
    margin: 0;
  
}

.wecome_section span{
    width: 80px;
    height: 4px;
    background-color: #4ac9f0;
    background-color: var(--prime-color);
    display: flex;
    margin-top: 4px;

}

.callout{
    width: 100%;
    height: auto;
    padding: 10px 25px;
    border-left: 0.25rem solid #4ac9f0;
    border-left: 0.25rem solid var(--prime-color);
}

.callout h6{
    margin: 0;
    padding: 0;
    
}
.cborder img{
    border: 15px solid #230c0c;
}
.btn-info{
    color: #fff!important;
}
/********************** ourwork ************************/
.ourwork{
    width: 100%;
    height: auto;
    background-color: #023041;
    background-color: var(--second-color);
}
.ourwork h3{
    font-size: 1.8rem;
    color: #fff;
    margin: 10px 0;
    font-weight: 500;
    text-transform: uppercase;
}

.ourwork span{
    width: 80px;
    height: 4px;
    background-color: #4ac9f0;
    background-color: var(--prime-color);
    display: flex;
    margin-top: 4px;

}

.workbox {
    width: 100%;
    height: auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.thumbnail {
    width: 220px;
    height: 220px;
    border-radius: 100%;
    background-color: #fff;
    overflow: hidden;
    display: flex;
}

.workbox p{
    margin: 10px 0;
    color: #fff;
    font-size: 15px;
}

/************************ customer_review *************************/
.customer_review{
    width: 100%;
    height: 60vh;
    background-image: url(/better_clean/static/media/customer_review.61749d1d.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    position: relative;
    padding: 10px 0;
    display: flex;
    align-items: center;
    
}

.customer_review::after{
    content: "";
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.9);
    position: absolute;
    left: 0;
    top: 0;
}

.abs{
    z-index: 999;
}


.customer_review h5{
    font-size: 1.8rem;
    color: #fff;
    margin: 10px 0;
    font-weight: 500;
    text-transform: uppercase;
}
.customer_review .border_bot{
    width: 80px;
    height: 4px;
    background-color: #4ac9f0;
    background-color: var(--prime-color);
    display: flex;
    margin-top: 4px;
    margin: 0 auto;
}

.slider{
    width: 100%;
    height: auto;
    display: flex;
}

.thumb{
    width: 150px;
    height: 150px;

}
.carousel-item{
    flex-direction: column;
}

.papper{
    width: 60%;
    height: auto;
    text-align: center;
}
.papper h6{
  margin: 20px 0;
  color: #fff;
  text-transform: capitalize;
  font-size: 20px;
}

.papper p{
    color: #fff;
    font-size: 14px;
    margin: 28px 0;
}

/****************** requestcall *********************/

.requestcall{
    width: 100%;
    height: auto;
    display: flex;
    padding: 75px 0 0;
}
.bgFirst{
   background-color: #4ac9f0;
   background-color: var(--prime-color);
   position: relative;
}


.bgFirst::before {
    content: "";
    width: 102px;
    height: 100%;
    background-color: #4ac9f0;
    position: absolute;
    right: -49px;
    top: 0;
    -webkit-transform: skew(-12deg);
            transform: skew(-12deg);
    z-index: 1;
}

.formCont {
    width: 75%;
    height: auto;
    z-index: 999;
    margin-left: auto;
    position: relative;
}



.requestcall h2{
    font-size: 1.8rem;
    color: #fff;
    margin: 10px 0;
    font-weight: 500;
    text-transform: uppercase;
}

.requestcall span {
    width: 80px;
    height: 4px;
    background-color: #fff;
    display: flex;
    margin-top: 4px;
    margin-bottom: 30px;
}

.formCont .form-control{
   border-top: none;
   border-left: none;
   border-right: none;
   background: transparent;
   border-radius: 0;
}

.formCont .form-control::-webkit-input-placeholder{
    color: #fff;
    font-size:14px;
    font-weight: 300;
    padding-left: 0;
}

.formCont .form-control::placeholder{
    color: #fff;
    font-size:14px;
    font-weight: 300;
    padding-left: 0;
}
.formCont .form-control::-webkit-input-placeholder:focus{
    color: #4ac9f0;
    color: var(--prime-color);
   
}
.formCont .form-control::placeholder:focus{
    color: #4ac9f0;
    color: var(--prime-color);
   
}

.submitBtn{
    background-color: #023041!important;
    margin-top: 30px;
    padding: 5px 20px!important;
    color: #fff!important;
}

/************************ footer **************************/
.footer{
    width: 100%;
    height: auto;
    background-color: #252525;
    text-align: center;
}
.footer h5{
  font-size: 1rem;
  color: #fff;
  margin: 15px 0;

}

.footer ul{
    margin: 0;
    padding: 0;
    list-style-type: none;
    display: flex;
    align-items: center;
}

.footer ul li{
    margin: 0 5px;
}

.footer ul li a{
    width: 30px;
    height: 30px;
    border-radius: 50px;
    background-color: #000;
}

.copywrite{
    width: 100%;
    height: auto;
    background-color: #4ac9f0;
    background-color: var(--prime-color);
    text-align: center;
}

.copywrite p{
    margin: 0;
    padding: 0;
    font-size: 14px;
    font-weight: normal;
    color: #fff;
}

/***************SupperHero****************/

.superHero{
   background-color: #f1f1f1;
}

.superHero h1{
 font-size: 1.8rem;
 text-transform: uppercase;
 margin: 0 0 30px 0;

}

.formCont.logi{
    width: 100%;
    height: auto;
    z-index: 999;
    
}

.formCont.logi .form-control::-webkit-input-placeholder{
    color: #333!important;
}

.formCont.logi .form-control::placeholder{
    color: #333!important;
}

/*************************** Media Queries ******************************/

@media (min-width: 150px) and (max-width: 480px) {
    .cards {
        width: 100%!important;
        height: auto;
        margin-bottom: 50px;
    }
    .maincard {
        flex-direction: column;
    }
    .cborder{
        margin-bottom: 20px;
    }

    .callout {margin-top: 30px;}

    .workbox {
    margin-bottom: 30px;
    }

    .customer_review {
        width: 100%;
        height: auto;
    }
    .requestcall {
        flex-direction: column;
    }

    .formCont {
        width: 100%;
    
    }

    .navbar-toggler-icon {
        display: inline-block;
        width: 1rem!important;
        height: 1rem!important;
        vertical-align: middle;
        background-repeat: no-repeat;
        background-position: center;
        background-size: 100%;
        background-image: url(data:image/png;base64,);
    }

    .cont{
        margin: 10px 0!important;
        padding: 0;
        justify-content: center!important;
        align-items: center!important;
    }

    .headIcon ul{
        margin: 10px 0!important;
        display: flex!important;
        justify-content: center!important;
    }

    .bgFirst::before {
        content: "";
        display: none;
    }


}


